body {
  color: #fff;
  background-color: #000;
  position: relative;
}

h2, p {
  margin: 0;
}

.error {
  display: none;
}

.breed-select {
  margin-bottom: 40px;
}

.cat-info {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.info-container {
  flex-direction: column;
  justify-content: center;
